import stocks from "../../src/data/stocks"
const state = {
    stocks: []
}

const getters = {
    stocks(state) {
        return state.stocks
    }
}


const mutations = {
    SET_STOCKS(state, stocks) {
        return state.stocks = stocks
    },
    RND_STOCKS(state) {
        return state.stocks.
            forEach(stock => {
                stock.price = Math.round(stock.price * (1 + Math.random() -0.5))
            });
    }
}

const actions = {
    initStocks({ commit }) {
        commit('SET_STOCKS', stocks)
    },
    buyStocks({ commit }, order) {
        commit('BUY_STOCKS', order)
    },
    randomizeStocks({ commit }) {
        commit('RND_STOCKS')
    }
}



export default {
    state,
    getters,
    mutations,
    actions
}