<template>
  <div class="nav-holder">
    <nav class="navbar navbar-expand-lg navbar-light bg-light">
      <div class="container">
        <router-link class="navbar-brand" to="/">Stock Trader</router-link>
        <button
          class="navbar-toggler"
          type="button"
          data-toggle="collapse"
          data-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span class="navbar-toggler-icon"></span>
        </button>

        <div class="collapse navbar-collapse" id="navbarSupportedContent">
          <ul class="navbar-nav mr-auto">
            <router-link active-class="active" tag="li" to="/Portfolio" exact
              ><a class="nav-link">Portfolio</a></router-link
            >
            <router-link active-class="active" tag="li" to="/Stocks"
              ><a class="nav-link">Stocks</a></router-link
            >
          </ul>
          <div class="my-2 my-lg-0">
            <ul class="navbar-nav">
              <li class="nav-item" tag="li">
                <a class="nav-link" @click="endDay">End Day</a>
              </li>
              <li class="nav-item dropdown">
                <a
                  class="nav-link dropdown-toggle"
                  href="#"
                  id="navbarDropdown"
                  role="button"
                  data-toggle="dropdown"
                  aria-haspopup="true"
                  aria-expanded="false"
                >
                  Save & Load
                </a>
                <div class="dropdown-menu" aria-labelledby="navbarDropdown">
                  <a class="dropdown-item" @click="saveData">save Data</a>
                  <a class="dropdown-item" @click="loadData">Load Data</a>
                </div>
              </li>
              <li class="nav-link">
                <b> Funds : {{ funds | currency }} </b>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </nav>
  </div>
</template>

<script>
import { mapActions } from "vuex";
export default {
  data(){
    return{
      myData:[]
    }
  },
  computed: {
    funds() {
      return this.$store.getters.funds;
    },
  },
  methods: {
    ...mapActions({
      randomizeStocks:"randomizeStocks",
      fetchData:"loadData"
    }),
    endDay() {
      this.randomizeStocks();
    },
    saveData() {
      const data = {
        funds: this.$store.getters.funds,
        stockPortfolio: this.$store.getters.stockPortfolio,
        stocks: this.$store.getters.stocks,
      };
      this.$http.put("data.json", data);
    },
    loadData(){
      this.fetchData()
    }
  },
};
</script>

